.projects-container {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  font-family: "Poppins", sans-serif;
  width: 100%;
  padding-bottom: 95%;
}

.projects-container > h1 {
  text-align: center;
  padding-bottom: 5%;
}

.project-list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10px;
  grid-auto-rows: minmax(100px, auto);
  padding-left: 50px;
  padding-right: 50px;
  text-align: center;
}

.contact-main-container {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 4%;
  padding-bottom: 5%;
  width: 100%;
  font-family: "Poppins", sans-serif;
}

.contact-container {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  font-family: "Poppins", sans-serif;
  width: 100%;
}

.form-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form-container > div {
  width: 80%;
  padding-bottom: 5%;
  position: relative;
}

.form-container > div > label {
  position: absolute;
  left: 8px;
}

input[type="text"] + label,
input[type="email"] + label {
  bottom: 60%;
  transition: all 0.2s;
  touch-action: manipulation;
}

input:not(:-ms-input-placeholder) + label {
  font-size: smaller;
  color: #d91e18;
  bottom: 80px;
  left: 4px;
  opacity: 0.5;
}

input:not(:placeholder-shown) + label,
input:focus + label {
  font-size: smaller;
  color: #d91e18;
  bottom: 80px;
  left: 4px;
  opacity: 0.5;
}
::-webkit-input-placeholder {
  opacity: 0;
  -webkit-transition: inherit;
  transition: inherit;
}

input:focus::-webkit-input-placeholder {
  opacity: 1;
}

textarea:focus + label {
  display: none;
}

input[type="text"],
input[type="email"],
textarea {
  width: 100%;
  margin-bottom: 10%;
  padding: 5px;
  box-sizing: border-box;
  border: 1px solid #9facbc;
  outline: none;
  font-size: large;
}

input[type="text"],
input[type="email"] {
  padding: 2% 2%;
  border: none;
  border-bottom: 1px solid #9facbc;
}

textarea {
  height: 50%;
  min-height: 200px;
  max-width: 100%;
  min-width: 100%;
  max-height: 400px;
  overflow-y: auto;
  font-size: large;
}

input[type="submit"] {
  outline: none;
  border-radius: 5px;
  height: 2em;
  font-size: medium;
  cursor: pointer;
  border-style: none;
  color: rgb(255, 255, 255);
  background: #d91e18;
}

input[type="submit"]:hover {
  box-shadow: 0px 1px 4px black;
}

@media screen and (min-width: 768px) {
  input[type="text"] + label,
  input[type="email"] + label {
    bottom: 70%;
  }

  input:not(:-ms-input-placeholder) + label {
    bottom: 100%;
    left: 4px;
    opacity: 0.5;
  }

  input:not(:placeholder-shown) + label,
  input:focus + label {
    bottom: 100%;
    left: 4px;
    opacity: 0.5;
  }
}

@media screen and (min-width: 1024px) {
  .form-container > div {
    padding-bottom: 0;
  }
}

@media screen and (min-width: 1440px) {
  input[type="text"],
  input[type="email"],
  textarea {
    margin-bottom: 6%;
  }
}

